}

@keyframes reveal-up {
  from {
    opacity: 0;
    transform: translateY(30px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.reveal-animation {
  animation: .6s ease-out forwards reveal-up;
}

.reveal-animation:first-child {
  animation-delay: .1s;
}

.reveal-animation:nth-child(2) {
  animation-delay: .2s;
}

.reveal-animation:nth-child(3) {
  animation-delay: .3s;
}

.reveal-animation:nth-child(4) {
  animation-delay: .4s;
}

.reveal-animation:nth-child(5) {
  animation-delay: .5s;
}

.reveal-animation:nth-child(6) {
  animation-delay: .6s;
}

